<template>
  <div class="main">

    <div class="bloginfo">
      <el-page-header @back="goBack" content="博客详情页面">
      </el-page-header>

      <div class="blogtitle">
        <p>{{blogtitle}}</p>
      </div>
      <div class="blogowner">
        <p>{{blogowner}} &nbsp;&nbsp;发布于&nbsp;&nbsp;{{blogdate}}</p>
      </div>
      <el-divider></el-divider>
      <div class="blogcontent">
        <pre>{{blogcontent}}</pre>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name: "BlogInfo",

    data() {
      return {
        blogtitle: '',
        blogcontent: '',
        blogdate: '',
        blogowner: ''

      }
    },

    created() {
      this.getinfo();

    },
    methods: {
      goBack(){
        this.$router.go(-1);
      },
      getinfo() {
        var blogid = this.$route.query.blogid;

        let param = new URLSearchParams();
        param.append("id", blogid)

        this.$axios({
          method: "post",
          url: "/blog/getblogbyid",
         data:param
        }).then(result => {
          let blog = result.data.blog[0]
          this.blogtitle = blog.title;
          this.blogcontent = blog.content;
          this.blogdate = blog.cdate;
          this.blogowner = blog.user.username;

        }).catch(error => {
          console.log(error);
        })
      }
    }
  }
</script>

<style scoped>
  .bloginfo {

    background: #ffffff;
    border-radius: 4px;
    width: 100%;


  }

  .bloginfo .blogtitle{
    margin-top: 20px;

  }
  .bloginfo .blogtitle p{
    text-align: center;
    font-size: 18px;
    color: #303133;
  }
  .bloginfo .blogowner{
    margin-bottom: -20px;
  }
  .bloginfo .blogowner p{
    color: #909399;
    text-align: right;
    font-size: 13px;
    font-style: oblique;
    white-space:nowrap;
  }
  .bloginfo .blogcontent p{
    padding: 30px;
    text-indent: 2em;
    line-height:1.7;
  }
</style>






